@import '../../styles/variables.scss';

.surah{
    padding: 10px 10px 10px 0;
    width: 100%;
    background: $dark;
    min-height: 100px;
    min-height: 100px;
    float: left;
    .english-name{
        font-size: 14px;
        font-family: GS;
        color: $primary;
        border-left: 2px solid $primary;
        width: 80%;
        float: left;
    }
    .number{
        float: right;
        font-family: GS;
        color: $primaryFill;
        font-size: 13px;
    }
    .arabic-name{
        font-family: GSLight;
        color: $primaryFill;
        font-size: 15px;
        margin-left: 13px;
        width: 100%;
        float: left;
    }
    .english-meaning{
        font-family: GS;
        color: $primaryFill;
        font-size: 11px;
        margin-left: 13px;
        width: 100%;
        float: left;
    }
    .rev-type{
        font-family: GS;
        color: $primaryFill;
        font-size: 11px;
        margin-left: 13px;
        width: 50%;
        float: left;
    }
    .ayahs{
        font-family: GS;
        color: $primaryFill;
        font-size: 11px;
        //margin-left: 13px;
        float: right;
    }
} 

