@import '../../../styles/variables.scss';
.arabic-ayah{
    width: 100%;
    text-align: right;
    font-family: Lateef;
    color: $primaryFill;
    font-size: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid rgba(163, 164, 165, 0.12156862745098039);
    line-height: 40px;
    direction: rtl;
    .text{
        padding-right: 10px !important;
        color: #ab854e;
        p{
            font-family: GS;
            font-size: 14px;
            font-family: GS;
            font-size: 14px;
            text-align: right;
            direction: ltr;
            line-height: 21px;
            color: $primaryFill
        }
    }
    .number{
        border: 1px solid;
        height: 29px;
        border-radius: 14px;
        font-size: 15px;
        line-height: 27px;
        text-align: center;
        margin-top: 7px;
        border-color: rgba(169, 174, 180, 0.5);
        color: rgba(169, 174, 180, 0.5);
    }
    .the-word:hover{
        color: $secondaryFill;
        cursor: pointer;
    }
    .play-but{
        font-family: GS;
        font-size: 16px;
        //color: #A3A4A5;
        color: rgba(0, 255, 0, 0.43) !important;
        cursor: pointer;
    }
    
}
@media only screen and (min-width: 768px) {
    .arabic-ayah{
        text-align: center;
        .number{
            border: none;
        }
        .text{
            p{
                text-align: center
            }
        }
    }
    
}
.ant-popover-inner-content{
    color: $primary !important;
}
.ant-popover-inner{
    background: $dark !important;
}