@import './variables.scss';

.container-fluid{
  padding: 0;
}
body{
  color: $foreground;
  background: $background;
}
.bg-dark {
  background-color: $dark !important;
}

.app-area{
  padding: $globalPadding;
  margin-top:50px;
}

@font-face {
  font-family: GS;
  src: url(../fonts//GillSans/GillSans.ttf);
}


@font-face {
  font-family: GSBold;
  src: url(../fonts/GillSans/GillSans-Bold.ttf);
}

@font-face {
  font-family: GSBoldItalic;
  src: url(../fonts/GillSans/GillSans-BoldItalic.ttf);
}

@font-face {
  font-family: GSItalic;
  src: url(../fonts/GillSans/GillSans-Italic.ttf);
}

@font-face {
  font-family: GSLight;
  src: url(../fonts/GillSans/GillSans-Light.ttf);
}

@font-face {
  font-family: GSLightItalic;
  src: url(../fonts/GillSans/GillSans-LightItalic.ttf);
}
@font-face {
  font-family: p1;
  src: url(../fonts/p1.ttf);
}
@font-face {
  font-family: Lateef;
  src: url(../fonts/LateefRegOT.ttf);
}



.ant-input:focus{
  box-shadow: none !important;
}
.ant-input:focus{
  border-color: $primary;
}
.ant-input-affix-wrapper:hover .ant-input:not(.ant-input-disabled) {
  border-color: $primary;
}


.ant-tabs-ink-bar{
  background-color: $primary;
  min-width: 72px !important;
}
.ant-tabs-bar{
  border-bottom: none;
}
.ant-tabs-nav {
  .ant-tabs-tab{
    color: $primaryFill;
  }
  .ant-tabs-tab-active{
    color: $primary;
  }
  
  .ant-tabs-tab:hover{
    color: $secondaryFill;
  }
}
.ant-tabs-nav-container-scrolling{
  padding-left: 0;
  padding-right: 0;
}

.ant-slider-handle.ant-tooltip-open {
  border-color: $primary;
}
.ant-slider-track {
  background-color: $primary; 
}
.ant-slider:hover .ant-slider-track {
  background-color: $primary;
}
.ant-tooltip-placement-top{
  width: 46px;
  .ant-tooltip-inner{
    text-align: center
  }
}
.label-set{
  color: $primaryFill;
  font-family: GSLight;
}
.ant-slider-handle{
  border-color: $primary;
}
.setting-icon{
  color: $primaryFill
}
.material-input{
    background: transparent;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid $primaryFill;
    padding-left: 0;
    color: $primaryFill
}
.speed-button{
  background: transparent;
  color: $primaryFill;
}

.speed-button:hover, .speed-button:focus, .speed-button:active, .speed-button.active {
  background: transparent;
  text-decoration: none;
  border: 1px solid $primary;
  color: $primary;
}
.ant-modal-header,
.ant-modal-footer
{
  border: none;
  .ant-modal-title{
    color: $primaryFill;
  }
  background: $dark
}
.ant-modal-body{
  color: $primaryFill;
  background: $background;
}
.ant-modal-content{
  .ant-modal-close-x{
    color: $primaryFill;
  }
}
.ant-message{
  z-index: 2000;
  .ant-message-notice-content{
    background: $background !important;
    color: $primary;
  }
  
}
.ant-select{
  width: 100%;
  .ant-select-selection{
    background: transparent;
      border: none;
      border-bottom: 1px solid $primary;
      border-radius: 0;
      .ant-select-selection-selected-value{
        color: $primaryFill;
      }
      .ant-select-selection__rendered{
        margin: auto 0 auto 0;
      }
  }
  .ant-select-arrow{
    color: $primaryFill;
  }
}