@import '../../../styles/variables.scss';

.drop-down{
    button
    {
        background: transparent;
        color: $primary;
        border: none; 
        border-bottom: 1px solid $primary; 
        margin-left: 0 !important;
        padding-left: 0;
        border-radius: 0;
        width: 100%;
        text-align: left
    }
    button:hover
    {
        background: transparent;
        color: $primary;
    }
}